import { ADD, DEL, GET } from "./userTypes";

export const get = (users) => {
	return {
		type: GET,
		users: users,
		message: "Users loaded."
	};
};

export const del = (id) => {
	return {
		type: DEL,
		id: id,
		message: "User deleted."
	};
};

export const add = (user) => {
	return {
		type: ADD,
		user: user,
		message: "User added."
	};
};
