import { HIDE, SHOW } from "./bannerTypes";

const bannerReducer = (state = { flag: false, message: "" }, action) => {
	switch (action.type) {
		case SHOW:
			return {
				flag: true,
				message: action.message
			};
		case HIDE:
			return {
				flag: false,
				message: action.message
			};
		default:
			return state;
	}
};

export default bannerReducer;
