import axios from "axios";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { add, del, get } from "../redux/user/userActions";
import { hide, show } from "../redux/banner/bannerActions";

const url = "https://dummyjson.com/users";

const Users = () => {
	const users = useSelector((state) => state.userReducer.users);
	const dispatch = useDispatch();
	useEffect(() => {
		axios.get(url).then((res) => {
			dispatch(get(res.data.users));
			const message = "Users loaded.";
			dispatch(show(message));
			setTimeout(() => dispatch(hide(message)), 2000);
		});
	}, []);

	const [firstName, setFirstName] = useState("");
	const addNewUser = () => {
		const len = users.length;
		let newId = -1;
		for (let i = 0; i < len; i++) {
			if (users[i].id !== i + 1) {
				newId = i + 1;
				break;
			}
		}
		const user = {
			id: newId < 0 ? len + 1 : newId,
			firstName: firstName
		};
		setFirstName("");
		return user;
	};

	return (
		<div>
			<div>
				<h2 style={{ textAlign: "center" }}>{users.length}</h2>
				<input
					type="text"
					placeholder="enter firstname"
					value={firstName}
					onChange={(e) => setFirstName(e.target.value)}
				/>
				<button
					disabled={firstName === ""}
					onClick={() => {
						dispatch(add(addNewUser()));
						const message = "added";
						dispatch(show(message));
						setTimeout(() => dispatch(hide(message)), 2000);
					}}>
					add
				</button>
			</div>
			<div>
				{users.map((user, index) => (
					<div
						key={index}
						style={{
							display: "flex",
							justifyContent: "space-between"
						}}>
						<label>{user.id}</label>
						<label>{user.firstName}</label>
						<button
							onClick={() => {
								dispatch(del(user.id));
								const message = "deleted";
								dispatch(show(message));
								setTimeout(() => dispatch(hide(message)), 2000);
							}}>
							delete
						</button>
					</div>
				))}
			</div>
		</div>
	);
};

export default Users;
