import { useSelector } from "react-redux";

const Banner = () => {
	const { flag, message } = useSelector((state) => state.bannerReducer);

	const style = {
		position: "absolute",
		top: flag ? "5px" : "-50px",
		right: "5px",
		width: "150px",
		height: "50px",
		border: "none",
		borderRadius: "5px",
		backgroundColor: "lightgrey",
		boxShadow: "0 5px 17px 3px lightgrey",
		transition: ".5s ease-in-out",
		display: "flex",
		justifyContent: "center",
		alignItems: "center"
	};

	return <strong style={style}>{message}</strong>;
};

export default Banner;
