import { ADD, DEL, GET } from "./userTypes";

const userReducer = (state = { users: [] }, action) => {
	switch (action.type) {
		case GET:
			return {
				users: action.users
			};
		case DEL:
			return {
				users: state.users.filter((user) => user.id !== action.id)
			};
		case ADD:
			const filterUsers = state.users.filter(
				(user) => user.firstName === action.user.firstName
			);
			if (filterUsers.length) {
				alert("duplicate User");
				return state;
			} else {
				return {
					users: [...state.users, action.user].sort((a, b) => a.id - b.id)
				};
			}
		default:
			return state;
	}
};

export default userReducer;
